.navbar{
    display: flex;
    justify-content: space-between;
    background-color: black;
    padding: 20px 30px;
    align-items: center;
}
.navbar-image{
    width: 70px;
}
.navbar-list li{
   display: inline; 
   padding: 20px;
   align-items: center;
}
.navbar-list a{
    text-decoration: none; 
    color: white;
    font-size: 17px;
    font-family: 'Montserrat', sans-serif;
}
.navbar-list a:hover{
   color: #61DAFB;
   font-weight: 700;
}
.hidden-links{
    height: 0;
    left: 0; 
    z-index: 1;
    top: 0;
    overflow-x: hidden;
}
.hidden-links li{
    list-style-type: none;
}

.shown-links{
    width: 100%;
    text-align: center;
    height: 300px;
    z-index: 2;
    transition: 0.5s;
    background-color: black;
    color: white;
    padding: 0;
    margin: 0;
}
.shown-links li{
    padding: 20px;
    list-style-type: none;
    
}
.shown-links a{
    text-decoration: none;
    color: white;
    font-size: 17px;
    list-style-type: none;
    font-family: 'Montserrat', sans-serif;
}
.shown-links a:hover{
    color: #61DAFB;
    font-weight: 700;
 }
.panel{
    color: white;
    display: none;
    position: absolute;
    right: 50px;
    top: 35px;
}
@media(max-width: 500px){
   .navbar-list{
       display: none;
   }
   .panel{
    display: block;
}

}
@media(min-width: 501px){
   .shown-links{
       display: none;
   }
 }

